@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.btn.btn-gray {
    background: #1e1e1e;
    border-radius: 0;
    color: #fff;
}

.btn.btn-gray:hover {
    background: #2c2c2c;
    border-radius: 0;
    color: #fff;
}
.footer-gray {
    color: #fff;
    background: #2c2c2c;
}

.footer-gray .copy-section{
    background: #1d1d1d;
}

.footer-gray .btn-link{
    color: #fff;
    font-size: 26px;
    transition: all .2s;
}

.footer-gray .btn-link:hover{
    color: #fff;
    transform: scale(1.2);
}
nav.nav-grey {
    background: transparent;
    min-height: 100px;
    font-size: 18px;
    transition: all .2s;
}

nav.nav-grey.active {
    background: #1d1d1d;
}

nav.nav-grey .nav-item {
    margin: 0 5px;
}

nav.nav-grey .nav-item .nav-link {
    color: #fff;
    cursor: pointer;
}

.navbar-brand {
    cursor: pointer;
}

nav.nav-grey .nav-item:hover .nav-link {
    color: #fff;
    border-bottom: 2px solid;
    margin-bottom: -2px;
}

nav.nav-grey .nav-item:hover .nav-link.lang {
    border-bottom: 0;
}

nav.nav-grey .nav-item .nav-link.lang::after {
    display: none;
}

nav.nav-grey .nav-item .nav-link.active {
    color: #fff;
    border-bottom: 2px solid;
    margin-bottom: -2px;   
}

button.navbar-toggler {
    border: none;
    font-size: 24px;
}

nav.nav-grey .lang-drop {
    width: 30px;
    border-radius: 0;
}
.main {
    position: relative;
    height: 100vh;
    width: 100%;
    /* max-width: 1440px; */
    overflow: hidden;
}

.main-content {
  position: absolute;
  background: rgba(0, 0, 0, 0.829);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  z-index: 2;
  flex-direction: column;
  padding: 0 10px;
}

.main-content .main-img {
  width: 250px;
}
